.bordeRreg {
  border-radius: 1rem;
  backdrop-filter: blur(1px);
  .modal-content {
    background-color: #0000;
    border: none;
    .modal-header {
      border-top-right-radius: 10px;
      border-top-left-radius: 10px;
      padding: 0;
      height: 4rem;
      margin: 0;
      font-weight: "400";
      font: 1.5em sans-serif;
    }
    .modal-footer {
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;
    }
    .form-control:focus {
      color: #212529;
      background-color: #fff;

      outline: 0;
      box-shadow: none;
    }
    ::placeholder {
      color: #7496b8;
      font-weight: lighter;
    }
  }
}
.captcha {
  color: #7496b8;
}
.LoginDarkHeader {
  background-color: #48494b !important;
  color: white !important;
  border-color: transparent;
}
.LoginLightHeader {
  color: black !important;
  background: #ecf5ff !important;
  border-color: transparent;
}
.centering {
  margin: 0 auto;
  padding: auto 0;
}
.inputiconleft {
  position: absolute;
  top: 50%;
  left: 15%;

  transform: translateY(-50%);
}
.resetbut {
  border-radius: 10px;

  border-color: transparent;
  background-color: transparent;

  transition: none;
  width: 50%;
  justify-self: center;
  margin: 0 26%;
  font-size: 16px;
  font-weight: lighter;
}
